.container {
    --color: #ed8936;
    min-width: 700px;
    max-width: 850px;
    margin: 0 auto;
    flex: 1.2;
    height: fit-content;
    min-height: 900px;
    box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 30px;
  }

  .header {
    display: flex;
    flex-direction: column;
  }
  
  .header .heading {
    font-size: 2.7rem;
    font-weight: 500;
    text-transform: capitalize;
  }
  
  .header .subHeading {
    color: var(--color);
    font-weight:bold;
    font-size: 1.1rem;
  }
  
  .header .links {
    margin-top: 15px;
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    row-gap: 10px;
  }
  
  .header .link {
    font-size: 0.875rem;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
    /* font-family: monospace; */
  }
  .header .link svg{
    color:var(--color);
    height:16px;
    width: 16px;
  }

  .main{
    display: flex;
    gap:30px;
  }
  .col1 ,.col2{
    flex:1.3;
    display: flex;
    flex-direction: column;
    gap:20px;
  }

  .section .sectionTitle{
    font-size: 1.4rem;
    font-weight: bold;
    width:100%;
    border-bottom:2.5px solid black;
  }

  .section .content{
    display:flex;
    flex-direction: column;
    gap:15px;
    padding: 10px 0;
  }
  .section .content .item{
    border-bottom: 1px dotted lightgray;
    gap:5px;
    display:flex;
    flex-direction: column;
  }
  .section .content .item:last-child{
    border-bottom: none;
  }
  .content .title{
    font-weight: 500;
    font-size:1.1rem;
    line-height: 1.3rem;
  }
  .content .subTitle{
    font-weight:600;
    color:var(--color);
    font-size: 1rem;
  }
  .content .overview{
    font-size: 0.875rem;;
  }
  .content .link{
    display: flex;
    gap:5px;
    font-size: 0.8rem;
    cursor:pointer;
    color:var(--color);
  }
  .content .link svg{
    height:14px;
    width:14px;
    color:var(--color);
  }
  .content .date , .content .location{
    display: flex;
    gap:5px;
    align-items: center;
    font-size: 0.875rem;
  }
  .content .date svg ,.content .location svg{
    height:14px;
    width:14px;
  }
  .content .points , .numbered{
    padding-left: 18px;
    font-size: 0.875rem;
    padding-top: 10px;
  }

  .numbered li {
    list-style-type: decimal;
  }
  
  .hidden {
    display: none;
  }
